:global(header) {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 50px;
  background-color: #444;
}
:global(header) .header {
  height: 40px;
}
:global(header) .header a {
  display: inline-block;
}
:global(header) .header h1 {
  width: 120px;
  height: 100%;
  font-size: 0;
  background: url("../../assets/images/logo.svg") no-repeat center center;
  float: left;
}
:global(header) .header .no-read-count {
  position: absolute;
  left: -30px;
  font-style: normal;
  font-size: 12px;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  background-color: #80bd01;
  border-radius: 50%;
}



