.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  width: 100%!important;
  height: 40px;
  background-color: #f6f6f6;
}
.navbar li {
  height: 30px;
  height: 20px;
  margin: 0 10px;
}
.navbar li a {
  color: #80bd01;
  padding: 3px 4px;
  display: inline-block;
  border-radius: 4px;
}
.navbar li a:hover,
.navbar li a:global(.active) {
  background-color: #80bd01;
  color: #fff;
}



