.detail {
  position: relative;
  width: 100%;
}
.detail .detail-content {
  width: 100%;
  padding: 20px;
  background-color: #fff;
}
.detail .type {
  display: inline-block;
  width: 32px;
  height: 18px;
  line-height: 18px;
  font-size: 12px;
  text-align: center;
  -ms-flex-item-align: center;
      align-self: center;
  margin-right: 10px;
  border-radius: 4px;
}
.detail .type.istop,
.detail .type.isgood {
  background-color: #80bd01;
  color: #fff;
}
.detail .type.isask,
.detail .type.isshare {
  background-color: #e5e5e5;
  color: #999;
}
.detail h2 {
  position: relative;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  padding: 20px;
  line-height: 40px;
  font-size: 30px;
  background-color: #fff;
}
.detail h2 .collect-btn {
  position: absolute;
  right: 20px;
  bottom: -20px;
}
.reply {
  margin-top: 10px;
}
.reply .reply-list > li {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  min-height: 80px;
  padding: 10px;
  border-top: 1px solid #e1e1e1;
  background-color: #fff;
}
.reply .reply-list > li .list-left {
  height: 100%;
}
.reply .reply-list > li .list-left img {
  width: 30px;
}
.reply .reply-list > li .list-right {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left: 10px;
  min-width: 1200px;
}
.reply .reply-list > li .list-right .right-header {
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-size: 14px;
}
.reply .reply-list > li .ups-or-reply {
  position: absolute;
  top: 10px;
  right: 0;
}
.reply .reply-list > li .ups-or-reply span {
  color: #999;
  margin-right: 5px;
  font-size: 14px;
  cursor: pointer;
}
.reply .reply-list > li .ups-or-reply span.is-uped {
  color: #000;
}
.reply .reply-list > li .ups-or-reply span:hover {
  color: #000;
}
.add-reply {
  background-color: #fff;
  margin-top: 10px;
  padding-bottom: 20px;
}



