.cn-button {
  border-radius: 5px;
}
.cn-button.large {
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
}
.cn-button.small {
  padding: 4px 8px;
  border-radius: 5px;
  font-size: 14px;
}
.cn-button.mini {
  padding: 2px 4px;
  border-radius: 5px;
  font-size: 12px;
}
.cn-button.primary {
  background-color: #0088cc;
  color: #fff;
}
.cn-button.primary:active {
  background-color: #006699;
}
.cn-button.success {
  background-color: #80bd01;
  color: #fff;
}
.cn-button.success:active {
  background-color: #5e8a01;
}
.cn-button.def {
  background-color: #e5e5e5;
  color: #000;
}
.cn-button.def:active {
  background-color: #cccccc;
}
.cn-button.text {
  background-color: transparent;
  color: #ccc;
}
.cn-button.text:hover {
  color: #fff;
}
.cn-button .rotate {
  -webkit-animation: rotate1 1s linear infinite;
          animation: rotate1 1s linear infinite;
  -webkit-transform-origin: 60% 50%;
      -ms-transform-origin: 60% 50%;
          transform-origin: 60% 50%;
}
.cn-button:disabled {
  opacity: 0.5;
}
@-webkit-keyframes rotate1 {
  from {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}
@keyframes rotate1 {
  from {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}



